/*公用*/
.btn{
	background: #fcc800;
}
.titlebox .line{
	background: #333;
}

/*阐述*/
.part-chanshu{
	background: #f3f3f3;
}
.part-chanshu img{
	display: block;
	width:100%;
}
.chanshu-ctn{
	margin-top: 0.1rem;
}

/*问题*/
.part-wenti{
	background:url(/assets/images/jingjiadt/jzxuanran/wenti-bj.jpg) no-repeat center;
	background-size: 100%;
	background-size: cover;
}
.part-wenti .titlebox h1{
	color:#fff;
}
.wenti-item{
	margin-bottom: 0.15rem;
} 
.wenti-lastitem{
	margin-bottom: 0;
}
.wenti-item p{
	font-size: 0.12rem;
	color: #fff;
	line-height: 0.3rem;
	float: left;
}
.wenti-item p:nth-of-type(1){
	width: 72%;
	padding-left: 3%;
	border: 1px solid #5e5e66;
	border-right: none;
}
.wenti-item p:nth-of-type(2){
	width: 24%;
	text-align: center;
	background: #fcc800;
	border: 1px solid #fcc800;
}
.part-wenti .btn{
	width:60%;
}

/*薪资*/
.part-xinzi{
	padding-bottom: 1.3rem;
}
.part-xinzi .titlebox span{
	color:#ff6600;
}
.part-xinzi img{
	width: 100%;
	display: block;
}
.xinzi-item{
	width: 100%;
	display: block;
}
.part-xinzi .btn{
	width:50%;
}

/*教学保障*/
.part-jxbz .titlebox h1{
	line-height: 0.3rem;
}
.jxbz-item{
	float:left;
	text-align: center;
	width: 33.33%;
}
.jxbz-item img{
	width:52%;
	margin: 0 auto;
	display: block;
}
.jxbz-threeitem{
	float:right;
}
.jxbz-item h2{
	color:#333;
	font-size: 0.14rem;
	line-height: 0.14rem;
	margin-top: 0.1rem;
}
.jxbz-ctn{
	margin-top: 0.15rem;
}

/*丝路实力*/
.part-slsl{
	background: #f5f5f5;
}
.slsl-item{
	float:left;
	text-align: center;
	width: 33.33%;
}
.slsl-item img{
	width:91%;
	margin: 0 auto;
	display: block;
}
.slsl-threeitem{
	float:right;
}
.slsl-item h2{
	color:#333;
	font-size: 0.12rem;
	line-height: 0.18rem;
	margin-top: 0.1rem;
}
.slsl-ctn{
	margin-top: 0.12rem;
}
.part-slsl .btn{
	width:40%;
}

/*表单1*/
.part-oneform{
	background:url(/assets/images/jingjiadt/jzxuanran/biaodan-bj1.jpg) no-repeat center;
	background-size: 100%;
}


/*讲师*/
.part-teacher{
	background: #f5f5f5;
}
.teacher-item{
	margin-bottom: 0.1rem;
}
.teacher-item img{
	float: left;
	width: 23.2%;
}
.teacher-text{
	float:right;
	width: 72.5%;
}
.teacher-text h2{
	color:#333;
	font-size: 0.15rem;
	line-height: 0.15rem;
	margin-top: 0.08rem;
}
.teacher-text h2 span,.teacher-text p{
	font-size: 0.12rem;
	line-height: 0.18rem;
	color:#999;
}
.teacher-text img{
	display: block;
	width: 100%;
	margin: 0.1rem 0;
}
.part-teacher .btn{
	width: 56%;
}

/*软件*/
.part-ruanjian{
	background: url(/assets/images/jingjiadt/jzxuanran/ruanjian-bj.jpg) bottom no-repeat;
	background-size: 100%;
}

.part-ruanjian img{
	display: block;
	width: 100%;
} 

/*作品*/
.part-zuopin img{
	display: block;
	width: 100%;
	margin-top: 0.2rem;
}
.part-zuopin .btn{
	width: 50%;
}

/*合作*/
.part-hezuo{
	padding-top: 40px;
}
.part-hezuo img{
	display: block;
	width: 100%;
}
.part-hezuo .btn{
	width: 40%;
}

/*表单2*/
.part-twoform{
	background:url(/assets/images/jingjiadt/jzxuanran/biaodan-bj2.jpg) no-repeat center;
	background-size: 100%;
}
